@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
	--gradient: linear-gradient( 135deg, #72EDF2 10%, #5151E5 100%);
}

* {
	box-sizing: border-box;
	line-height: 1.25em;
}

.clear {
	clear: both;
}
html {font-family: 'Montserrat', sans-serif;}
body {
	margin: 0;
	width: 100%;
	height: 100vh;
	font-family: 'Montserrat', sans-serif;
	background-color: #343d4b;
	display: flex;
	align-items: center;
	justify-content: center;
}

.container {
	border-radius: 25px;
	box-shadow: 0 0 70px -10px rgba(0, 0, 0, 0.2);
	background-color: #222831;
	color: #ffffff;
	height: 400px;
}

.weather-side {
	position: relative;
	height: 100%;
	border-radius: 25px;
	background-image: url("https://images.unsplash.com/photo-1559963110-71b394e7494d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=675&q=80");
	width: 300px;
	box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.2);
	transition: -webkit-transform 300ms ease;
	transition: transform 300ms ease;
	transition: transform 300ms ease, -webkit-transform 300ms ease;
	-webkit-transform: translateZ(0) scale(1.02) perspective(1000px);
	        transform: translateZ(0) scale(1.02) perspective(1000px);
	float: left;
}

.weather-side:hover {
	-webkit-transform: scale(1.1) perspective(1500px) rotateY(10deg);
	        transform: scale(1.1) perspective(1500px) rotateY(10deg);
}

.weather-gradient {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background-image:linear-gradient( 135deg, rgba(77, 168, 172, 0.6) 10%, rgba(52, 52, 146, 0.6) 100%);
	border-radius: 25px;
	opacity: 1;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.date-container {
	position: absolute;
	top: 25px;
	left: 25px;
}

.date-dayname {
	margin: 0;
}

.date-day {
	display: block;
}



.location {
	display: block;
	margin-top: 30px;
  border-top:1px solid rgba(255, 255, 255, 0.37);
  padding-top: 15px;
  font-weight: 500;
}
.city-name { font-weight: 600;}
.location-icon {
	display: inline-block;
	height: 0.8em;
	width: auto;
	margin-right: 5px;
}

.weather-container {
	position: absolute;
	bottom: 25px;
	left: 25px;
}

.weather-icon.feather {
	height: 60px;
	width: auto;
}

.weather-temp {
	margin: 0;
	font-weight: 700;
	font-size: 50px;
	margin-bottom: 10px;
}
.weather-temp span { font-size: 30px; padding-left: 10px; font-weight: 600; display: inline-block; -webkit-transform: translateY(-13px); transform: translateY(-13px); }

.weather-desc {
	margin: 0;
	text-transform: capitalize;
}

.info-side {
	position: relative;
	float: left;
	height: 100%;
	padding-top: 25px;
}

.today-info {
	padding: 15px;
	margin: 0 25px 25px 25px;
/* 	box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.25); */
	border-radius: 10px;
}

.today-info>div:not(:last-child) {
	margin: 0 0 10px 0;
}

.today-info>div .title {
	float: left;
	font-weight: 700;
}

.today-info>div .value {
	float: right;
}

.week-list {
	list-style-type: none;
	padding: 0;
	margin: 10px 35px;
	box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
	background: #
}

.week-list>li {
	float: left;
	padding: 15px;
	cursor: pointer;
	transition: 200ms ease;
	border-radius: 10px;
}

.week-list>li:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	background: #fff;
	color: #222831;
	box-shadow: 0 0 40px -5px rgba(0, 0, 0, 0.2)
}

.week-list>li.active {
	background: #fff;
	color: #222831;
	border-radius: 10px;
}

.week-list>li .day-name {
	display: block;
	margin: 10px 0 0 0;
	text-align: center;
}

.week-list>li .day-icon {
	display: block;
	height: 30px;
	width: auto;
	margin: 0 auto;
}

.week-list>li .day-temp {
	display: block;
	text-align: center;
	margin: 10px 0 0 0;
	font-weight: 700;
}

.location-container {
	padding: 25px 35px;
}

.location-button {
	outline: none;
	width: 100%;
	box-sizing: border-box;
	border: none;
	border-radius: 25px;
	padding: 10px;
	font-family: 'Montserrat', sans-serif;
	background-image: linear-gradient( 135deg, #72EDF2 10%, #5151E5 100%);
	background-image: var(--gradient);
	color: #ffffff;
	font-weight: 700;
	box-shadow: 0 0 30px -5px rgba(0, 0, 0, 0.25);
	cursor: pointer;
	transition: -webkit-transform 200ms ease;
	transition: transform 200ms ease;
	transition: transform 200ms ease, -webkit-transform 200ms ease;
}

.location-button:hover {
	-webkit-transform: scale(0.95);
	        transform: scale(0.95);
}

.location-button .feather {
	height: 1em;
	width: auto;
	margin-right: 5px;
}

.latlong { width:100%; margin-top:20px }
.date-container { right:25px }
.latlong { display:flex; justify-content:space-between }
.latlong-inner { width:calc(50% - 6px);  }
.latlong-inner span { display:block }
.latlong-inner .value { font-size:16px;margin-top:8px; font-weight:600 }
.latlong-inner .name { font-size:13px; font-weight: 500; }
.latlong-inner  { border:1px solid #FFF; border-radius:5px; padding:10px }


.modalwrap { background-color: #222831bd; position: fixed; left: 0; top: 0; bottom: 0; right: 0; height: 100%; width: 100%; z-index: 10; display: flex; align-items: center; justify-content: center; -webkit-backdrop-filter: blur(10px); backdrop-filter: blur(10px); }
.select-location-holder { position: relative; width: 320px; }
.select-location-holder input { border:0; padding: 15px 25px; border-radius: 25px; font-size: 16px; width: 100%;}
.select-location-holder input:focus { outline: none;}

.select-location-holder   .sub-btn { color:#FFF}
.select-location-holder .sub-btn { padding-top: 5px; background-color: #222831; width:40px; height:40px; border-radius: 50%; border:0; position: absolute; right: 5px; top: 5px; }
.select-location-holder .sub-btn-auto { padding-top: 5px; background-image: linear-gradient( -135deg, #72EDF2 10%, #5151E5 100%);; width:100%; height:50px; border-radius: 50px; border:0; color:#FFF; font-size: 16px; margin-top: 20px; cursor: pointer; }
.select-location-holder .sub-btn-auto  i { margin-right: 5px;}

@media screen and (max-width: 767px) {
	.container { height: auto; padding-top: 40px;}
	.weather-side { float: none;margin: auto;}
	.info-side { float: none;} 
	.weather-gradient { height: 370px; position: static;} 
	body { display: block;  }
}
